import BadgeOutlinedIcon from '@mui/icons-material/BadgeOutlined'
import FactCheckOutlinedIcon from '@mui/icons-material/FactCheckOutlined'
import ScreenSearchDesktopOutlinedIcon from '@mui/icons-material/ScreenSearchDesktopOutlined'
import { Box, Card, CardContent, Container, Stack, styled, Typography, useMediaQuery, useTheme } from '@mui/material'
import axios from 'axios'
import { useSnackbar } from 'notistack'
import { useEffect, useState } from 'react'
import { ReactComponent } from 'src/assets/undraw_about_us_page.svg'
import ClubList from 'src/components/ClubList'
import CircleIcon from '@mui/icons-material/Circle'
import { PureLightTheme } from 'src/theme/schemes/PureLightTheme'

const IndexImage = styled(ReactComponent)(() => ({
  width: '100%'
}))

const BenfitCard = styled(Card)(() => ({ backgroundColor: 'transparent', boxShadow: 'none', padding: 1 }))

const BenefitCardContent = styled(CardContent)(() => ({
  padding: 0,
  '&:last-child': {
    paddingBottom: 0
  }
}))

const BenfitBox = styled(Box)(() => ({
  width: '100%',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center'
}))

const BenefitCircleIcon = styled(CircleIcon)(() => ({
  color: PureLightTheme.colors.primary['main'],
  fontSize: '100px'
}))

const BenefitIconStyle = {
  position: 'absolute',
  margin: 'auto',
  left: 0,
  right: 0,
  top: 0,
  color: 'white',
  fontSize: '50px'
}

interface ApplyingClubType {
  id: number
  name: string
  type: string
  category: string
  location: string
}

function Index() {
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('sm'))
  const [clubs, setClubs] = useState(null)
  const { enqueueSnackbar } = useSnackbar()

  const count = matches ? 20 : 10

  useEffect(() => {
    axios
      .get<ApplyingClubType[]>(
        `${process.env.REACT_APP_APIHOST}/apply/clubs?page=${encodeURIComponent('1')}&count=${encodeURIComponent(
          `${count}`
        )}`
      )
      .then(res => {
        if (res.data?.length > 0) setClubs(res.data)
        else enqueueSnackbar('해당하는 동아리가 없습니다', { variant: 'warning' })
      })
      .catch(err => enqueueSnackbar(err.response.data?.message, { variant: 'error' }))
  }, [count])

  return (
    <>
      <Box
        width="100%"
        sx={{
          backgroundColor: 'white',
          display: 'flex',
          flexDirection: matches ? 'row' : 'column',
          justifyContent: 'space-around',
          alignItems: 'center',
          padding: theme.spacing(8, 0),
          [theme.breakpoints.down('md')]: {
            padding: theme.spacing(5, 0)
          }
        }}
      >
        <Container maxWidth="xs" sx={{ paddingBottom: matches ? 0 : 3 }}>
          <Typography variant={matches ? 'h1' : 'h3'} component="h1" gutterBottom>
            All the Clubs on KLUB!
          </Typography>
          <Typography variant="subtitle2" component="p">
            KLUB은 고려대학교 동아리를 위한 웹 서비스입니다. 카테고리와 소속 단위에 따라 어떤 동아리가 있는지
            확인해보세요. 동아리 운영진이라면 KLUB만의 획기적인 실시간 회원 관리 툴을 접해보세요!
          </Typography>
        </Container>
        <Container maxWidth="xs">
          <IndexImage />
        </Container>
      </Box>
      <Container
        maxWidth="md"
        sx={{
          padding: theme.spacing(5, 0),
          [theme.breakpoints.up('sm')]: {
            padding: theme.spacing(7, 0)
          }
        }}
      >
        <Typography variant={matches ? 'h2' : 'h3'} component="h1" gutterBottom sx={{ textAlign: 'center' }}>
          Key Benefits
        </Typography>
        <Stack
          direction={matches ? 'row' : 'column'}
          spacing={matches ? 4 : 2}
          justifyContent="center"
          alignItems={matches ? 'flex-start' : 'center'}
          sx={{ padding: theme.spacing(0, matches ? 0 : 7), paddingTop: 1.5 }}
        >
          <BenfitCard sx={{ width: matches ? '23%' : '100%' }}>
            <BenefitCardContent>
              <BenfitBox>
                <Box style={{ position: 'relative' }}>
                  <BenefitCircleIcon />
                  <ScreenSearchDesktopOutlinedIcon sx={{ ...BenefitIconStyle, bottom: 2 }} />
                </Box>
              </BenfitBox>
              <Typography variant="h4" component="h3" sx={{ textAlign: 'center', marginBottom: 0.3 }}>
                편리한 동아리 검색
              </Typography>
              <Typography display="block" variant="caption" sx={{ textAlign: 'center' }}>
                교내 동아리뿐만 아니라 연합 동아리까지 모두
                <Typography display="inline" sx={{ fontWeight: 'bold' }}>
                  {' KLUB '}
                </Typography>
                에서 찾아보세요!
              </Typography>
            </BenefitCardContent>
          </BenfitCard>
          <BenfitCard sx={{ width: matches ? '23%' : '100%' }}>
            <BenefitCardContent>
              <BenfitBox>
                <Box style={{ position: 'relative' }}>
                  <BenefitCircleIcon />
                  <BadgeOutlinedIcon sx={{ ...BenefitIconStyle, bottom: 9 }} />
                </Box>
              </BenfitBox>
              <Typography variant="h4" component="h3" sx={{ textAlign: 'center', marginBottom: 0.3 }}>
                모집과 홍보
              </Typography>
              <Typography display="block" variant="caption" sx={{ textAlign: 'center' }}>
                <Typography display="inline" sx={{ fontWeight: 'bold' }}>
                  {'KLUB '}
                </Typography>
                을 통해 바로 가입할 수 있도록 홍보 페이지 및 신청 폼 제작을 지원해요
              </Typography>
            </BenefitCardContent>
          </BenfitCard>
          <BenfitCard sx={{ width: matches ? '23%' : '100%' }}>
            <BenefitCardContent>
              <BenfitBox>
                <Box style={{ position: 'relative' }}>
                  <BenefitCircleIcon />
                  <FactCheckOutlinedIcon sx={{ ...BenefitIconStyle, bottom: 4 }} />
                </Box>
              </BenfitBox>
              <Typography variant="h4" component="h3" sx={{ textAlign: 'center', marginBottom: 0.3 }}>
                회원 명단, 출석 관리
              </Typography>
              <Typography display="block" variant="caption" sx={{ textAlign: 'center' }}>
                회원 정보뿐만 아니라 행사 출결 관리까지 모두
                <Typography display="inline" sx={{ fontWeight: 'bold' }}>
                  {' KLUB '}
                </Typography>
                에서 할 수 있어요!
              </Typography>
            </BenefitCardContent>
          </BenfitCard>
        </Stack>
      </Container>
      <Container sx={{ padding: theme.spacing(2, matches ? 0 : 2) }}>
        <Typography variant="h3" component="h2" gutterBottom sx={{ paddingBottom: 1 }}>
          현재 모집 중인 동아리예요
        </Typography>
        <ClubList clubs={clubs} />
      </Container>
    </>
  )
}

export default Index
